.table--responsive--lg {
		@include md-screen {
			thead {
				display: none;
			}
			tbody {
				tr {
					display: block;
					border-spacing: 0px 20px;
					border-radius: 0px;
					&:first-child {
						border-radius: 6px 6px 0px 0px !important;
					}
					&:last-child {
						border-radius: 0 0 6px 6px !important;
					}
					 &:nth-child(odd) {
					background-color: hsl(var(--white) /.1);
	                }
					&:last-child td {
						&:last-child {
							border-bottom: 0 !important;
						}
					}
					
					&:hover {
						transform: translateY(-7px);
					}
					td {
						display: flex;
						align-items: center;
						justify-content: space-between;
						gap: 15px;
						text-align: right;
						padding: 10px 15px;
						border: none;
						max-width: unset;
						&:last-child {
							border: none;
						}
						&:first-child {
							text-align: right;
							border-left: 0;
						}
						&::before {
							display: block;
							font-size: 17px; 
						}
					}
				}
			}
		}
		@include md-screen {
			tbody {
				tr {
					td {
						border-bottom: 1px solid hsl(var(--white) / .08);
					}
				}
			}
		}
}
