        }
        &.outline {
            border: 1px solid hsl(var(--violet));;
            background-color: transparent;
            color: hsl(var(--violet));
            &:hover, &:focus {
                background-color: hsl(var(--violet));;
                color: hsl(var(--white));
            }
        }
    }
}
/* ================================= Button Css End =========================== */