/* =========================== Banner Section Start Here =========================*/
.banner-section {
    position: relative;
    background: var(--banner-gradient);
    z-index: 1;
    overflow: hidden;
    padding-top: 0;
    padding-bottom: 0;
    @include lg-screen {
        padding-top: 50px;
    }
    @include md-screen {
        padding-top:25px;
    }
    @include sm-screen  {
        padding-top: 180px;
    }
    @include msm-screen {
        padding-top: 160px;
    }
    &.bg-img {
        @include md-screen {
            background-position: center right;
            object-fit: contain;
        }
    }

}
.planet-bg {
    position: absolute;
    left: 190px;
    top: 220px;
    z-index: -1;
}
.planet-small {
    position: absolute;
    right: 40px;
    top: 280px;
    z-index: -1;
}
.banner-content {
    position: relative;
    z-index: 9;
    @include sm-screen {
        text-align: center;
    }
    &__title {
        font-size: 50px;
        margin-bottom: 0;
        @include md-screen {
            font-size: 45px;
        }
        @include sm-screen {
            font-size: 35px;
        }
        @include msm-screen {
            font-size: 30px;
        }
    }
    &__desc {
        max-width: 600px;
        color: hsl(var(--white) / .8);
    }
    &__buttons {
        padding-top: 50px;
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        @include md-screen {
            padding-top: 30px;
        }
        @include sm-screen {
            justify-content: center;
            align-items: center;
        }
    }
}
/* ====city scene css start here==== */

.city-scene {
    position: relative;
    width: 856px;
    height: 800px;
    z-index: 1;
    @include lg-screen {
        height: 600px;
    }
    @include md-screen {
        height: 475px;
    }
    @include sm-screen {
        height: 195px;
        opacity: 0.5;
    }
    @include msm-screen {
        height: 130px;
    }
}
