/* ===========================train section start here=========================== */
.train-section {
    width: 100%;
    height: 75px;
    position: relative;
    z-index: 555;
    margin-top: -70px;
    overflow: hidden;
}
.train-wrapper {
    position: absolute;
    bottom: 0px;
    right: 0px;
    z-index: 100;
    transform: translateX(1200px);
    animation: train-move 12s linear infinite;
    animation-delay: 1s;
}
.train{
    position: relative;
    &::before {
        content: '';
        position: absolute;
        width: 160px;
        height: 28px;
        bottom: -2px;
        left: -158px;
        background: url(../../assets//images/shapes/train-light.png) no-repeat center;
        background-size: 160px;
        animation: 0.5s train-light ease-in-out infinite; 
    }
}
.railway {
    width: 100%;
    height: 5px;
    background: var(--railway-gradient);
    position: absolute;
    left: 0px;
    bottom: 0px;
}
@keyframes building-move {
    0% {
      transform: translateY(740px);
    }
  
    100% {
      transform: none;
    }
  }

  @keyframes train-move {
    0% {
      transform: translateX(1200px);
    }
  
    100% {
      transform: translateX(-5000px);
    }
  }
/* ===========================train section end here=========================== */